
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', sans-serif;
}

body {
  background-color: #f4f6f8;
  padding: 20px;
}

h1, h2 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
}

form {
  display: flex;
  gap: 10px;
  width: 100%;
  max-width: 500px;
  margin: 0 auto 20px auto;
}

input {
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #fff;
  width: 80%;
}

button {
  padding: 10px;
  font-size: 1rem;
  background-color: #1976d2;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease;
}

button:hover {
  background-color: #1565c0;
}

.task-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
  padding: 10px;
  justify-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.task-card {
  background: white;
  width: 100%;
  max-width: 300px;
  padding: 15px 20px;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task-card p {
  margin: 0;
  font-size: 1rem;
  color: #333;
}

.task-card button {
  background-color: #e53935;
  font-size: 0.9rem;
  padding: 6px 12px;
}

.task-card button:hover {
  background-color: #c62828;
}

@media (max-width: 424px) {
  form {
    flex-direction: column;
  }
  input {
    width: 100%;
  }

  .task-container {
    grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
  }
}
